import React from 'react'
import { Todo } from './Todo'
import { TodoAdd } from './TodoAdd'


export const Todos = ({ todos, setTodos }) => {

    const onDelete = (todo) => {

        setTodos(todos.filter((e) => {
            // console.log(e != todo)
            return e !== todo
        }))

    }

    const onAdd = (title, desc) => {

        let sno 

        todos.length === 0 ? sno = 1 : sno = todos[todos.length - 1].sno + 1

        const myTodo = {
            'sno': sno,
            'title': title,
            'desc': desc
        }
        setTodos([...todos, myTodo])
    }

  
    

    let myStyle = {
        minHeight: '70vh',
        margin: '10px 0'
    }


    return (
        <>
            <TodoAdd onAdd={onAdd} />

            <div className='container my-3' style={myStyle}>

                <h3 className='text-center'>Todos List</h3>
                {
                    todos.length === 0 ? 'No Todos To display!!!!! ' :
                        todos.map((todo) => {
                            return <Todo todo={todo} key={todo.sno} onDelete={onDelete} />
                        })
                }

            </div>
        </>
    )
}
