import React from 'react'

import { useState } from 'react'

export const TodoAdd = ({ onAdd }) => {

    const [title, setTitle] = useState('')
    const [desc, setDesc] = useState('')

    const submit = (e) => {
        e.preventDefault()
        if (!title || !desc) {
            alert('titl or desc cabbit bkabjed')
        }
        onAdd(title, desc)
        setTitle('')
        setDesc('')
    }


    return (
        <>
            <div className='container my-3'>

                <h2>Add Todo</h2>

                <form onSubmit={submit}>
                    <div className="form-group">

                        <label htmlFor="title">Enter Title : </label>
                        <input type="text" className="form-control" value={title} name="title" id="title" aria-describedby="helpId" placeholder="Title" onChange={(e) => { setTitle(e.target.value) }}></input>

                        <label htmlFor="task">Enter Task : </label>
                        <input type="text" className="form-control" name="desc" value={desc} id="task" aria-describedby="helpId" placeholder="Desc" onChange={(e) => { setDesc(e.target.value) }}  ></input>

                        {/* <i class="fas fa-check-square ">Mark as Done .</i> */}
                        <button type="submit" className="btn btn-success btn-sm mt-2">Add Todo</button>

                    </div>
                </form>
            </div>
        </>
    )
}
