// import logo from './logo.svg';
import './App.css';

import Header from './MyComp/Header' // it is in export default
import { Footer } from './MyComp/Footer'
import { About } from './MyComp/About'
import { Todos } from './MyComp/Todos'//it is const

import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom"
import { Login } from './MyComp/Login';

function App() {

  let initTodo;

  localStorage.getItem('todos') === null ? initTodo = [] : initTodo = JSON.parse(localStorage.getItem('todos'))


  const [todos, setTodos] = useState(initTodo)

  useEffect(() => {
    localStorage.setItem('todos', JSON.stringify(todos))
  }, [todos])


  return (
    <>
      <Router>

        <Header title='Vk-Todo-React' searchBar={true} />

        <Switch>

          <Route exact path='/' render = { ()=> {
            return <Todos todos={todos} setTodos={setTodos} />
          }}>
          </Route>

          <Route exact path='/about'>
            <About />
          </Route>

          <Route exact path='/login'>
            <Login />
          </Route>

        </Switch>

        <Footer />

      </Router>


    </>
  );
}

export default App;
// eslint-disable-next-line